@import "./common";
#block-styler {
  position: absolute;
  @include modalStyle;
  border-radius: 5px;
  width: fit-content;
  max-width: inherit;
  overflow-x: scroll;
  .inner {
    position: relative;
    width: 100%;
    height: 32px;
    display: flex;
    flex-direction: row;
  }
  .btn {
    width: max-content;
    height: 100%;
    padding: 0 8px;
    transition: backgroundColor 20ms ease-in 0s;
    display: flex;
    align-items: center;
    font-size: 14px;
    &:hover {
      background-color: $itemHover;
    }
    span,
    svg {
      height: 14px;
    }
    span {
      min-width: fit-content;
    }
  }
  #menu-command {
    display: flex;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 50;
  }
  .blockStyler__btn-link {
    svg:first-child {
      margin-right: 4px;
    }
  }
  .btn-comment {
    svg:first-child {
      margin-right: 4px;
    }
  }
  .style {
    display: flex;
  }
  .style__btn-bold {
    font-weight: bolder;
  }
  .style__btn-italic {
    font-style: italic;
  }
  .style__btn-underline {
    text-decoration: underline;
  }
  .style__btn-lineThrough {
    text-decoration: line-through;
  }
  .blockStyler__btn-color {
    .btn {
      font-weight: bolder;
    }
  }

  .arrow-down {
    color: $mud;
    margin-left: 3px;
  }
}
#block-styler__color {
  position: absolute;
  z-index: 30;
}

#block-styler__side-menu {
  position: relative;
  & > .inner {
    max-height: inherit;
  }
  #menu-command {
    margin: 0;
    z-index: 0;
  }
  #menu {
    position: relative;
  }
}
